<template>
    <div class="row" v-if="ready">
      <div class="col-12">
        <div> <!--general-->
        <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleSubmit('form1')" >
        <div class="card">
          <div class="card-header">
            <strong>Reimbursement Form</strong>
          </div>            
          <div class="card-body card-block">
            <div class="row form-group">
                <div class="col col-md-3"><label class="form-control-label">Name of Applicant</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <input type="text" class='form-control' v-model='applicant' readonly required/>
                </div>
            </div>
            <div class="row form-group">
                <div class="col col-md-3"><label class="form-control-label">Committee / Department</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <select v-model="dept" class='form-control select' @change="load_info" required>
                        <option v-for="dep in dept_list">{{ dep.department }}</option>
                    </select>
                </div>
            </div>
            <div v-if="proc_id!=''" class="row form-group">
                <div class="col col-md-3"><label class="form-control-label">Procurement ID</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <input type="text" class='form-control' v-model='proc_id' readonly required/>
                </div>
            </div>
            <div style="display:flex;flex-direction:row">
                <label style="font-size:0.85em;color:rgba(0,0,0,0.7)">Description of Goods Purchased / Services Delivered:</label>
            </div>
            <div class="row form-group" style="margin-left:-1.26em;margin-right:-1.26em">
            <table class="table table-striped first-td-padding">
                <thead>
                    <tr>
                        <th style='width:15%'></th>
                        <th v-if="counter>=1" style='font-size:1.5em!important'>Item 1<div class="fa fa-plus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="new_row();cal_sum()"/></th>
                        <th v-if="counter>=2" style='font-size:1.5em!important'>Item 2<div class="fa fa-minus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="remove_row();cal_sum()"/></th>
                        <th v-if="counter>=3" style='font-size:1.5em!important'>Item 3<div class="fa fa-minus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="remove_row();cal_sum()"/></th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Category</td>
                        <td v-if="counter>=1">
                            <select class="form-control select" v-model="cat[0]" @change="load_bp(0)" required>
                                <option v-for="cat in cat_list">{{ cat.name }}</option>
                                <option :value="'Circular'">Fee-collecting Circular</option>
                            </select>
                        </td>
                        <td v-if="counter>=2">
                            <select class="form-control select" v-model="cat[1]" @change="load_bp(1)" required>
                                <option v-for="cat in cat_list">{{ cat.name }}</option>
                                <option :value="'Circular'">Fee-collecting Circular</option>
                            </select>
                        </td>
                        <td v-if="counter>=3">
                            <select class="form-control select" v-model="cat[2]" @change="load_bp(2)" required>
                                <option v-for="cat in cat_list">{{ cat.name }}</option>
                                <option :value="'Circular'">Fee-collecting Circular</option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Nature</td>
                        <td v-if="counter>=1">
                            <textarea class="form-control" maxlength="100" rows="2" v-model="nature[0]" required/>
                        </td>
                        <td v-if="counter>=2">
                            <textarea class="form-control" maxlength="100" rows="2" v-model="nature[1]" required/>
                        </td>
                        <td v-if="counter>=3">
                            <textarea class="form-control" maxlength="100" rows="2" v-model="nature[2]" required/>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Location</td>
                        <td v-if="counter>=1">
                            <input required readonly type="text" class="form-control" v-model="loc[0]" placeholder="Other location"/>
                        </td>
                        <td v-if="counter>=2">
                            <input required readonly type="text" class="form-control" v-model="loc[1]" placeholder="Other location"/>
                        </td>
                        <td v-if="counter>=3">
                            <input required readonly type="text" class="form-control" v-model="loc[2]" placeholder="Other location"/>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Reason for application</td>
                        <td v-if="counter>=1">
                            <textarea class="form-control" rows="2" maxlength="100" v-model="reason[0]" required/>
                        </td>
                        <td v-if="counter>=2">
                            <textarea class="form-control" rows="2" maxlength="100" v-model="reason[1]" required/>
                        </td>
                        <td v-if="counter>=3">
                            <textarea class="form-control" rows="2" maxlength="100" v-model="reason[2]" required/>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Budget Plan Category</td>
                        <td v-if="counter>=1">
                            <input v-if="cat[0]=='Circular'" type='text' class='form-control' v-model="bp[0]" placeholder="Enter Circular Code" readonly required @keyup='check_circular(0)'/>
                            <select v-if="cat[0]!='Circular'" class="form-control select" v-model="bp[0]" required @change="new_bp(0)">
                                <option v-for="bpl in bp_list_0" :value="bpl.description">({{ bpl.tag }}) {{ bpl.description }}</option>
                            </select>
                        </td>
                        <td v-if="counter>=2">
                            <input v-if="cat[1]=='Circular'" type='text' class='form-control' v-model="bp[1]" placeholder="Enter Circular Code" readonly required @keyup='check_circular(1)'/>
                            <select v-if="cat[1]!='Circular'" class="form-control select" v-model="bp[1]" required @change="new_bp(1)">
                                <option v-for="bpl in bp_list_1" :value="bpl.description">({{ bpl.tag }}) {{ bpl.description }}</option>
                            </select>
                        </td>
                        <td v-if="counter>=3">
                            <input v-if="cat[2]=='Circular'" type='text' class='form-control' v-model="bp[2]" placeholder="Enter Circular Code" readonly required @keyup='check_circular(2)'/>
                            <select  v-if="cat[2]!='Circular'" class="form-control select" v-model="bp[2]" required @change="new_bp(2)">
                                <option v-for="bpl in bp_list_2" :value="bpl.description">({{ bpl.tag }}) {{ bpl.description }}</option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Fund Source</td>
                        <td v-if="counter>=1">
                            <input v-if="cat[0]=='Circular'" type='text' class='form-control' :value="bp[0]" v-model="fund[0]" placeholder="Enter Circular Code" readonly/>
                            <select v-if="cat[0]!='Circular'" class="form-control select" v-model="fund[0]" required>
                                <option v-for="f in fund_list_0">{{ f.fundsrc }}</option>
                            </select>
                        </td>
                        <td v-if="counter>=2">
                            <input v-if="cat[1]=='Circular'" type='text' class='form-control' :value="bp[1]" v-model="fund[1]" placeholder="Enter Circular Code" readonly/>
                            <select v-if="cat[1]!='Circular'" class="form-control select" v-model="fund[1]" required>
                                <option v-for="f in fund_list_1">{{ f.fundsrc }}</option>
                            </select>
                        </td>
                        <td v-if="counter>=3">
                            <input v-if="cat[2]=='Circular'" type='text' class='form-control' :value="bp[2]" v-model="fund[2]" placeholder="Enter Circular Code" readonly/>
                            <select v-if="cat[2]!='Circular'" class="form-control select" v-model="fund[2]" required>
                                <option v-for="f in fund_list_2">{{ f.fundsrc }}</option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Amount HK$</td>
                        <td v-if="counter>=1">
                            <input type="number" step=0.01 class="form-control" v-model='amount[0]' :max='fund_max[0]' required @change="cal_sum(0)" value=0/>
                        </td>
                        <td v-if="counter>=2">
                            <input type="number" step=0.01 class="form-control" v-model='amount[1]' :max='fund_max[1]' required  @change="cal_sum(1)" value=0/>
                        </td>
                        <td v-if="counter>=3">
                            <input type="number" step=0.01 class="form-control" v-model='amount[2]' :max='fund_max[2]' required  @change="cal_sum(2)" value=0/>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Invoice Number</td>
                        <td colspan='100%'> 
                            <input type="text" class="form-control" v-model="invoice" placeholder="Separate multiple invoice with ," required readonly/>
                        </td>
                    </tr>
                </tbody>
            </table>            
            </div>
            <div class="row form-group">
                <div class="col col-md-12">
                    <div style="display:flex;flex-direction:row">
                        <div style="width:15%;padding-left:0.3%">Recipient type :</div>
                        <input type="radio" name="type_rec" v-model="rtype" value="staff" style="margin-left:2em;margin-top:0.2em" @click="clr" required><label>Staff</label>
                        <input type="radio" name="type_rec" v-model="rtype" value="company" style="margin-left:1em;margin-top:0.2em" @click="clr"><label>Company</label>
                    </div>
                    <div style="display:flex;flex-direction:row;padding-left:0" class='col-6'>
                        <label style="padding-top:0.4em;;padding-left:0.55%;padding-right:0;width:30.45%;">Name of recipient :</label>
                        <input type="text" v-model="search_rec" class="form-control input col-8"  @keyup="search_record($event)" @keydown.esc="cls_table" required/>
                    </div>
                    <div v-if="new_sup==true" style='padding-top:0.4em;'>
                        <div style='display:flex;flex-direction:row;width:100%;padding-left:0' class='col-6'>
                            <div class='col-8' style='padding-left:0'>
                                <input type="text" class="form-control" placeholder="Enter Company Address" v-model="company_address" required/>
                            </div>
                            <div class='col-4' style='padding-left:0;padding-right:0'>
                                <input type="text" class="form-control" placeholder="Enter Company Contact" v-model="company_contact" required/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
             <div class="row form-group">
                <div class="col col-md-12">
                    <div style="display:flex;flex-direction:row">
                        <!--
                    <div v-if="(signed==false)&&((applicant_sec==applicant)||(this.level!=2))" class="col-6" style="padding-left:0">
                        <div style="display:flex;flex-direction:row;padding-left:0">
                            <label style="width:30.45%;padding-top:0.4em;;padding-left:0.55%;padding-right:0">    
                                Security Code : 
                            </label>
                                <input type="password" v-model='pwd' class="form-control col-5"/>                   
                                <button type="button" class="form-control col-3" style='padding-left:0' ref="submitcode" @click="post_ssc">Sign now !</button>         
                        </div>  
                    </div>
                    <div v-if="signed" style="display:flex;flex-direction:row;">
                        <label class="form-control-label" style="margin-right:1em;padding-top:0.4em;margin-top:2.5em">    
                            Signature 
                        </label>
                            <img :src="sign_data[0].signature" style="width:auto;height:5em;border-bottom:1px solid black;margin-left:0.5em"/>        
                    </div>   -->      
                    <label class="form-control-label" style="margin-left:auto;margin-top:2.5em">
                        <b>Total Amount in HK$: {{ sum }}</b>
                    </label>
                    </div>
                </div>
            </div>
            <div v-html="message">{{message}}</div>
            </div>     

        <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm" :disabled="processing">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="button" class="btn btn-danger btn-sm" @click="reject_req">
              <i class="fa fa-ban"></i> Reject
            </button>
        </div>
      </form>
    </div>  
      <!--check form-->  
    </div> 
        <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="recep_data"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_record_sec"                
                >
            <template v-slot:items="props">
                <td><a href="" :id= props.item.username v-on:click.prevent="select_user_sec(props.item.name)">Select</a></td>
                <td>{{ props.item.name }}</td>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div> 
        <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="recep_data"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_record"
                >
            <template v-slot:items="props">
                <tr v-on:click="select_user(props.item.name)" style='cursor:pointer'>
                    <td><a href="" :id= props.item.username v-on:click.prevent="select_user_sec(props.item.name)">Select</a></td>
                    <td>{{ props.item.name }}</td>
                </tr>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div> 
    </div>
</template>


<script>
import axios from 'axios';
export default {
    data(){          
        var tmr =document.getElementById('tmr_date').value;
        return{
            is_new_circular:false,
            checking:0,
            circular_val:[0,0,0],
            circular_list:[],
            hv_circular:false,
            data_list:[],
            processing:false,
            proc_id:'',
            period:'',
            loc_type:['Internal','Internal','Internal'],
            req_tag:'',
            invoice:'',
            dept_list:[],
            sub_dept_list:[],
            asset_list:[],
            fund_list:[],
            check_num:true,
            //table
            sum:0,
            cat:[],
            nature:[],
            loc:[],
            reason:[],
            bp:[],
            bp_tag:[],
            fund:[],
            amount:[0,0,0],
            pwd:'',
            ready:false,
            new_sup:false,
            company_address:'',
            company_contact:'',
            //table
            //sign
            sign_data:[],
            fund_cost:[],         
            loc_list:[],
            bp_list:[],
            bp_list_0:[],
            bp_list_1:[],
            bp_list_2:[],
            fund_list_0:[],
            fund_list_1:[],
            fund_list_2:[],
            cat_list:[],
            recep_data:[],
            fund_max:[],
            fund_max_ini:[],
            counter:1,
            applicant_sec:'',
            hv_record_sec:false,
            pdf:'',
            level:0,
            message:'',
            user_check:'',
            signed:false,
            rtype:'',
            search_rec:'',
            hv_record:false,
            headers: [
            { text: 'Action',value :'action',sortable: false},
            { text: 'Name',value:'name',sortable: false},
            ], 
        }  
    },
async created(){
    await axios.get('../permission.php')
    .then(
        (response)=>{
            this.level = response.data[0].proc_level;
        }
    )
    await axios.get('Procurement/shared_backend/check_user_setup.php')
    .then(
        (response)=>{
            this.user_check = response.data[0].signed;
            if(!(this.user_check=='set')){
                this.$router.push('../user_setup');
            }
        }
    )
    await axios.get('Procurement/department/backend/dept_member.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
            }
        ); 
    await axios.get('../user_info.php')
        .then(
            (response)=>{
                this.applicant = response.data[1];
            }
        ); 
    await axios.get('Procurement/shared_backend/location.php')
        .then(
            (response)=>{
                this.loc_list = response.data;

            }
        );     
    await this.fetch_req_info();
    var formData = new FormData();
    formData.append('dept',this.dept);
    await axios.post('Procurement/budget/backend/asset.php',formData)
            .then(
                (response)=>{
                    this.cat_list = response.data;
                    var i=0;
                    for(i=0;i<3;i++){
                        this.cat[i] = this.cat_list[0].name;
                    }
                }
            )
    formData.append('cat',this.cat[0]);
    await axios.post('Procurement/shared_backend/fetch_bp_item.php',formData)
            .then(
                (response)=>{
                    this.bp_list_0 = response.data;
                    this.fund[0] = this.bp_list_0[0].fundsrc;
                    this.bp_list_1 = response.data;
                    this.fund[1] = this.bp_list_1[0].fundsrc;  
                    this.bp_list_2 = response.data;
                    this.fund[2] = this.bp_list_2[0].fundsrc;
                    this.bp[0] = this.bp_list_0[0].description;
                    this.bp_tag[0] = this.bp_list_0[0].tag;
                    this.bp[1] = this.bp_list_1[0].description;
                    this.bp_tag[1] = this.bp_list_1[0].tag;
                    this.bp[2] = this.bp_list_2[0].description;
                    this.bp_tag[2] = this.bp_list_2[0].tag;
                }
            )
    formData.append('bp',this.bp[0]);
    await axios.post('Procurement/shared_backend/fetch_bp_fund.php',formData)
            .then(
                (response)=>{
                    this.fund_list_0 = response.data;
                    this.fund_list_1 = response.data;
                    this.fund_list_2 = response.data;
                    
                    this.fund_max[0] = parseFloat(this.fund_list_0[0].cost_total) - parseFloat(this.fund_list_0[0].expense);
                    this.fund_max_ini[0]=this.fund_max[0];
                                     
                    this.fund_max[1] = parseFloat(this.fund_list_1[0].cost_total) - parseFloat(this.fund_list_1[0].expense);
                    this.fund_max_ini[1]=this.fund_max[1];
                    
                    this.fund_max[2] = parseFloat(this.fund_list_2[0].cost_total) - parseFloat(this.fund_list_2[0].expense);
                    this.fund_max_ini[2]=this.fund_max[2];
                }
            )   
    this.fetch_check_circular();
    this.ready = true;        
    //asset category of department   
  },
async mounted(){
    //asset category of department   

},   
    methods:{
        check_circular(event){
            var key = this.bp[event];
            var upper = key.toUpperCase();
            this.checking = event;
            this.fund[event] = upper;
            this.bp[event] = upper;
            this.$forceUpdate();
            if(key.length>0){
                var formData = new FormData();
                formData.append('key',upper);
                formData.append('dept',this.dept);
                axios.post("Procurement/reimbursement/backend/circular_code.php",formData)
                    .then(
                        (response)=>{
                            if(response.data!='empty'){
                                this.circular_list = response.data;
                                this.is_new_circular = false;
                                this.hv_circular = true;
                            } else {
                                this.is_new_circular = true;
                                this.hv_circular = false;
                            }
                        }
                    )
            }           
        },
        fetch_check_circular(){
            var i = 0;
            for(i=0;i<this.data_list.length;i++){
                if(this.data_list[i].asset_cat=='Circular'){
                    this.cat[i] = 'Circular';
                    this.bp[i] = this.data_list[i].budget_src;
                    this.fund[i] = this.data_list[i].fundsrc;
                }
            }        
        },
        async reject_req(){
            var msg = "Confirm Rejecting Reimbursement Request : "+this.req_tag+"?";
            this.$confirm(msg).then(
                async()=>{
                    var formData = new FormData();
                    formData.append('tag',this.req_tag);
                    formData.append('period',this.period);
                    await axios.post('Procurement/reimbursement/backend/cancel_req.php',formData)
                        .then(
                            (response)=>{
                                if(response.data=='OK'){
                                    this.$router.push('req_reimb');
                                }
                            }
                        )
                }
            )               
        },
        async fetch_req_info(){
            var tag = document.getElementById('tmp_id').value;
            var formData = new FormData();
            this.req_tag = tag;
            formData.append('tag',tag);
            await axios.post('Procurement/reimbursement/backend/fetch_req_info.php',formData)
                .then(
                    (response)=>{
                        this.counter = response.data.length;
                        this.data_list = response.data;
                        var i = 0;
                        for(i=0;i<this.counter;i++){
                            this.proc_id = response.data[0].proc_id;
                            this.nature[i] = response.data[i].nature;
                            var loc_tmp = response.data[i].location;
                            loc_tmp = loc_tmp.split(',');
                            this.loc[i] = loc_tmp;
                            this.invoice = response.data[0].invoice_num;
                            this.amount[i] = response.data[i].amount;
                            this.rtype = response.data[0].receiver_type;
                            this.search_rec = response.data[0].receiver_name;
                            this.reason[i] = response.data[i].reason;
                            this.applicant = response.data[0].app_id;
                            this.period = response.data[0].period;                     
                        }
                        this.dept = response.data[0].dept_full;
                    }
                )
            var i = 0;
            this.sum = 0;
            for(i=0;i<this.counter;i++){
                this.sum = Number(this.sum) + Number(this.amount[i]);
            }
        },
        load_supdept(event){
            var formData = new FormData();
            formData.append('dept',this.budget.dept);
            axios.post('Procurement/budget/backend/subdept.php',formData)
            .then(
                (response)=>{
                    this.sub_dept_list = response.data;                  
                    if(this.sub_dept_list==''){
                        this.display_sub = false;
                    } else {
                        this.budget.sub = this.sub_dept_list[0].subdept_full;
                        this.display_sub = true;
                    }
                }
            )
            axios.post('Procurement/budget/backend/asset.php',formData)
            .then(
                (response)=>{
                    this.asset_list = response.data;
                    this.budget.cat = this.asset_list[0].name;
                }
            )
            axios.post('Procurement/budget/backend/fund.php',formData)
            .then(
                (response)=>{
                    this.fund_list = response.data;
                    this.budget.fund[0] = this.fund_list[0].name;
                }
            )
        },
        async load_info(event){   //change dept update all
        var formData = new FormData();
        formData.append('dept',this.dept);
        await axios.post('Procurement/budget/backend/asset.php',formData)
                .then(
                    (response)=>{
                        this.cat_list = response.data;
                        var i=0;
                        for(i=0;i<3;i++){
                            this.cat[i] = this.cat_list[0].name;
                        }
                    }
                )
        formData.append('cat',this.cat[0]);
        await axios.post('Procurement/shared_backend/fetch_bp_item.php',formData)
            .then(
                (response)=>{
                    this.bp_list_0 = response.data;
                    this.bp_list_1 = response.data;
                    this.bp_list_2 = response.data;
                    this.bp[0] = this.bp_list_0[0].description;
                    this.bp_tag[0] = this.bp_list_0[0].tag;
                    this.bp[1] = this.bp_list_1[0].description;
                    this.bp_tag[1] = this.bp_list_1[0].tag;
                    this.bp[2] = this.bp_list_2[0].description;
                    this.bp_tag[2] = this.bp_list_2[0].tag;
                }
            )
        formData.append('bp',this.bp[0]);   
        await axios.post('Procurement/shared_backend/fetch_bp_fund.php',formData)
            .then(
                (response)=>{
                    this.fund_list_0 = response.data;
                    this.fund_list_1 = response.data;
                    this.fund_list_2 = response.data;
                    this.fund[0] = this.fund_list_0[0].fundsrc;
                    this.fund_max[0] = parseFloat(this.fund_list_0[0].cost_total) - parseFloat(this.fund_list_0[0].expense);
                    this.fund_max_ini[0]=this.fund_max[0];
                    this.fund[1] = this.fund_list_1[0].fundsrc;                   
                    this.fund_max[1] = parseFloat(this.fund_list_1[0].cost_total) - parseFloat(this.fund_list_1[0].expense);
                    this.fund_max_ini[1]=this.fund_max[1];
                    this.fund[2] = this.fund_list_2[0].fundsrc;
                    this.fund_max[2] = parseFloat(this.fund_list_2[0].cost_total) - parseFloat(this.fund_list_2[0].expense);
                    this.fund_max_ini[2]=this.fund_max[2];
                }
            )
        },
        empty_loc(id){
            this.loc[id] = '';
        },
        load_bp(event){
            var target = '';
            var formData = new FormData();       
            formData.append('dept',this.dept);
            formData.append('cat',this.cat[event]);
            axios.post('Procurement/shared_backend/fetch_bp_item.php',formData)
            .then(
                (response)=>{
                    if(response.data!=''){
                        if(event==0){
                        this.bp_list_0 = response.data;
                        this.fund_list_0 = response.data;
                        this.bp[0] = this.bp_list_0[0].description;
                        this.bp_tag[0] = this.bp_list_0[0].tag;
                        this.fund[0] = this.fund_list_0[0].fundsrc;
                        this.fund_max[0] = parseFloat(this.bp_list_0[0].cost_total) - parseFloat(this.bp_list_0[0].expense);
                        this.fund_max_ini[0]=this.fund_max[0];
                        } else if(event==1){
                            this.bp_list_1 = response.data;
                            this.fund_list_1 = response.data;
                            this.bp[1] = this.bp_list_1[0].description;
                            this.bp_tag[1] = this.bp_list_1[0].tag;
                            this.fund[1] = this.fund_list_1[0].fundsrc;
                            this.fund_max[1] = parseFloat(this.bp_list_1[0].cost_total) - parseFloat(this.bp_list_1[0].expense);
                            this.fund_max_ini[1]=this.fund_max[1];
                        } else if(event==2){
                            this.bp_list_2 = response.data;
                            this.fund_list_2 = response.data;
                            this.bp[2] = this.bp_list_2[0].description;
                            this.bp_tag[2] = this.bp_list_2[0].tag;
                            this.fund[2] = this.fund_list_2[0].fundsrc;
                            this.fund_max[2] = parseFloat(this.bp_list_2[0].cost_total) - parseFloat(this.bp_list_2[0].expense);
                            this.fund_max_ini[2]=this.fund_max[2];
                        }; 
                        this.new_bp(event);
                    } else {
                        this.fund[event] = '';
                        this.bp[event] = '';
                    }                     
                }
            )      
        },
        post_ssc(event){
            var formData = new FormData();
            formData.append('pwd',this.pwd);
            axios.post('Procurement/reimbursement/backend/check_sign.php',formData)
            .then(
                (response)=>{
                    this.sign_data = response.data;
                    if(response.data[0].signature==undefined){
                        this.message = response.data;
                    } else {
                    this.signed=true;
                    this.message='';
                    }
                }
            )
        },
        cal_num(event){
            this.total = this.unit_cost * this.quantity;
            if(isNaN(this.total)){
                this.total = '...';
                this.check_num = false;
            }
            this.fund_cost[0] = this.total;
            },

        new_row(event){
            if(this.counter<3){
            this.counter = this.counter+1;
            }
        },
        remove_row(event){
            if(this.counter>1){
            this.counter = this.counter-1;
            }
        },
        submit_form(event){

        },
        select_user(event){
            this.search_rec = event;
            this.hv_record = false;
            this.new_sup = false;
        },
        select_user_sec(event){
            this.applicant_sec = event;
            this.hv_record_sec = false;
        },
         search_record_sec(event){
            var formData = new FormData();
            formData.append('key', this.applicant_sec);
            axios.post('RequestPortal/backend/search_user_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_record_sec=false; 
                    } else {
                        this.recep_data = response.data;
                        this.hv_record_sec=true;
                    }
                }); 
        },
        search_record(event){
            if(!(event.keyCode==27)){
                var formData = new FormData();
                formData.append('key', this.search_rec);
                if(this.rtype=='staff'){
                axios.post('RequestPortal/backend/search_user_list.php',formData)
                .then(
                    (response)=>{
                        if(response.data == "Empty"){
                        this.hv_record=false; 
                        } else {
                            this.recep_data = response.data;
                            this.recep_data.username = response.data.username;
                            this.recep_data.name = response.data.name;
                            this.hv_record=true;
                        }
                    }); 
                } else if(this.rtype=='company'){
                axios.post('Procurement/shared_backend/supplier_list.php',formData)
                .then(
                    (response)=>{
                        if(response.data == "Empty"){
                        this.hv_record=false; 
                        this.new_sup=false;
                        } else if(response.data == "new"){
                            this.new_sup = true;
                        }
                        else {                       
                            this.recep_data = response.data;
                            this.recep_data.username = response.data.username;
                            this.recep_data.name = response.data.name;
                            this.hv_record=true;
                            this.new_sup=false;
                        }
                    }); 
                } 
            }
        },
        cls_table(){
            this.hv_record=false;
        },
        clr(event){
            this.search_rec='';
            this.hv_record=false;
        },
        addfile_pdf(event){
             this.pdf = this.$refs.pdf_file.files;
        },
        cal_sum(event){
            if(this.amount[event]==''){
                this.amount[event] = 0;
            };
            // 0 case
            if(event==0){
                if((this.cat[0]==this.cat[1])&&(this.bp[0]==this.bp[1])&&(this.fund[0]==this.fund[1])){
                    this.fund_max[1] = parseFloat(this.fund_max_ini[0]);
                    this.fund_max[1] = parseFloat(this.fund_max[1])-parseFloat(this.amount[0])-parseFloat(this.amount[2]);
                }
                if((this.cat[0]==this.cat[2])&&(this.bp[0]==this.bp[2])&&(this.fund[0]==this.fund[2])){
                    this.fund_max[2] = parseFloat(this.fund_max_ini[0]);
                    this.fund_max[2] = parseFloat(this.fund_max[2])-parseFloat(this.amount[0])-parseFloat(this.amount[1]);
                }
            }
            else if(event==1){
                if((this.cat[1]==this.cat[0])&&(this.bp[1]==this.bp[0])&&(this.fund[1]==this.fund[0])){
                    this.fund_max[0] = parseFloat(this.fund_max_ini[1]);
                    this.fund_max[0] = parseFloat(this.fund_max[0])-parseFloat(this.amount[1])-parseFloat(this.amount[2]);
                }
                if((this.cat[1]==this.cat[2])&&(this.bp[1]==this.bp[2])&&(this.fund[1]==this.fund[2])){
                    this.fund_max[2] = parseFloat(this.fund_max_ini[1]);
                    this.fund_max[2] = parseFloat(this.fund_max[2])-parseFloat(this.amount[1])-parseFloat(this.amount[0]);
                }
            } else if(event==2){
                if((this.cat[2]==this.cat[0])&&(this.bp[2]==this.bp[0])&&(this.fund[2]==this.fund[0])){
                    this.fund_max[0] = parseFloat(this.fund_max_ini[2]);
                    this.fund_max[0] = parseFloat(this.fund_max[0])-parseFloat(this.amount[2])-parseFloat(this.amount[1]);
                }
                if((this.cat[2]==this.cat[1])&&(this.bp[2]==this.bp[1])&&(this.fund[2]==this.fund[1])){
                    this.fund_max[1] = parseFloat(this.fund_max_ini[2]);
                    this.fund_max[1] = parseFloat(this.fund_max[1])-parseFloat(this.amount[2])-parseFloat(this.amount[0]);
                }
            }

            //update max for same src
            var i = 0;
            this.sum = 0;
            for(i=0;i<this.counter;i++){
            this.sum = Number(this.sum) + Number(this.amount[i]);
            };
        },
        new_bp(event){
            var formData = new FormData();
            formData.append('dept',this.dept);
            formData.append('cat',this.cat[event]);
            formData.append('bp',this.bp[event]);
            axios.post('Procurement/shared_backend/fetch_bp_fund.php',formData,{
            headers: {
                'Content-Type': 'multipart/form-data'
            }
          })
                .then(
                    (response)=>{                                              
                        if(event==0){    
                            this.fund_list_0 = response.data;                       
                            this.fund[0] = this.fund_list_0[0].fundsrc;
                            this.fund_max[0] = parseFloat(this.fund_list_0[0].cost_total) - parseFloat(this.fund_list_0[0].expense);
                            this.fund_max_ini[0]=this.fund_max[0];
                        } else if(event==1){
                            this.fund_list_1 = response.data;
                            this.fund[1] = this.fund_list_1[0].fundsrc;                   
                            this.fund_max[1] = parseFloat(this.fund_list_1[0].cost_total) - parseFloat(this.fund_list_1[0].expense);
                            this.fund_max_ini[1]=this.fund_max[1];
                        } else if(event==2){
                            this.fund_list_2 = response.data;
                            this.fund[2] = this.fund_list_2[0].fundsrc;
                            this.fund_max[2] = parseFloat(this.fund_list_2[0].cost_total) - parseFloat(this.fund_list_2[0].expense);
                            this.fund_max_ini[2]=this.fund_max[2];
                        }                        
                    }
                )
        },
        async handleSubmit(event){
            //if(!(this.sum>5000)){
                //if(this.signed==false){
                    this.processing = true;
                    var formData = new FormData();
                    var i=0;
                    var count_files = 0;
                    if(this.proc_id!=''){
                        formData.append('proc_id',this.proc_id);
                    }
                    formData.append('req_tag',this.req_tag);
                    formData.append('applicant',this.applicant);
                    formData.append('invoice',this.invoice);
                    formData.append('dept',this.dept);
                    //counter loop
                    for(i=0;i<this.counter;i++){
                        formData.append('asset_cat'+i,this.cat[i]);
                        formData.append('nature'+i,this.nature[i]);
                        formData.append('location'+i,this.loc[i]);
                        formData.append('reason'+i,this.reason[i]);
                        formData.append('bp'+i,this.bp[i]);
                        formData.append('fundsrc'+i,this.fund[i]);
                        formData.append('amount'+i,this.amount[i]);
                    }
                    for( var i = 0; i < this.pdf.length; i++ ){
                    let file = this.pdf[i];
                    formData.append('files'+i, file);
                    count_files=count_files+1;
                    }
                    formData.append('count_files', count_files);
                    //counter loop
                    formData.append('total',this.sum);
                    formData.append('r_type',this.rtype);
                    formData.append('r_name',this.search_rec);
                    formData.append('sign_stat',"false");     
                    formData.append('counter',this.counter);
                    await axios.post('Procurement/reimbursement/backend/req_reimb.php',formData)
                    .then(
                        (response)=>{
                                this.$alert('Successfully submitted record').then(
                                    ()=>{
                                        this.processing = false;                                     
                                    }
                                )
                                this.$router.push('req_reimb');                          
                        }
                    )
                //} 
                /*
            } else {
                this.$alert('As Cost is over 5000, procurement is required !');
            }
            */
        }
    },
        
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
</style>


